.switch {
    float: right;
    margin-right: var(--size-margin-35);
    margin-top: -45px;
    >p, >.bar-circle, .bar, .circle {
        display: inline-block;
    }
    >p {
        margin: 0;
        cursor: pointer;
    }
    >.bar-circle{
        margin-left: 30px;
        margin-right: 0px;
        >.bar {
            width: 60px;
            height: 2px;
            background-color: var(--color-off-black);
            vertical-align: middle;
            line-height: 1rem;
        }
        >.circle {
            width: 30px;
            height: 30px;
            background-color: var(--color-off-black);
            vertical-align: middle;
            line-height: 1rem;
            border-radius: 50%;
            transition: transform 0.4s;
            transform: translateX(-15px);
            &.icon {
                transform: translateX(-75px);
            }
        }
    }
}


@media screen and (max-width: 600px) {
    .switch {
        margin-top: 0;
        >.bar-circle{
            >.bar {
                width: 30px;
            }
            >.circle {
                width: 20px;
                height: 20px;
                transform: translateX(-10px);
                &.icon {
                    transform: translateX(-50px);
                }
            }
        }
    }
}